.footer-container {
  background-color: #fff;
  position: fixed;
  z-index: 101;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  display: flex;
  box-shadow: 0 -0.026667rem 0.053rem rgba(0,0,0,.1);
  .user-handle {
    flex: 1;
    text-align: center;
    .user-handle-btn {
      position: absolute;
      padding: 3%;
      border-radius: 50%;
      background-color: rgb(255, 42, 63);
      transform: translate(-50%, -25%);
      color: #fff;
      font-weight: bolder;
      box-shadow: 0 0 0.1rem #888;
    }
  }
}

.footer-mask {
  height: 1rem;
  visibility: hidden;
}
@primary-color: #FF2A42;